}
.boton-amarillo:hover {
  cursor: pointer;
  background-color: #97ad37;
}

.boton-amarillo-block {
  background-color: #a8c03d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
}
.boton-amarillo-block:hover {
  cursor: pointer;
  background-color: #97ad37;
}

.boton-verde {
  background-color: #68a008;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-verde:hover {
  cursor: pointer;
  background-color: #588807;
}

.boton-rojo {
  background-color: red;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-rojo:hover {
  cursor: pointer;
  background-color: #e60000;
}

.boton-verde-block {
  background-color: #68a008;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
}
.boton-verde-block:hover {
  cursor: pointer;
  background-color: #588807;
}

.boton-rojo-block {
  background-color: #cc0000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
