}
.boton-rojo-block:hover {
  cursor: pointer;
  background-color: #b30000;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .formulario {
  color: #FFFFFF;
}
.dark-mode .formulario legend, .dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode table td {
  color: #FFFFFF;
}
.dark-mode .entrada-blog {
  background-color: #333333;
}
.dark-mode .testimonial-persona img {
  border: 0.5rem solid white;
}

.slider-title {
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 11;
  margin-bottom: 2rem;
}
.slider-title div {
  display: inline-block;
  color: #FFFFFF;
  padding-left: 5rem;
  padding-right: 5rem;
  border-radius: 50%;
}
.slider-title div h1 {
  margin: 0;
  text-shadow: 0px 0px 20px rgb(0, 0, 0);
}

.propiedades-slider {
  margin-top: -29rem;
  position: relative;
  z-index: 10;
}
@media (min-width: 768px) {
  .propiedades-slider {
    margin-top: -40rem;
  }
}

.slider {
  position: relative;
  overflow: hidden;
  width: auto;
  max-width: 80rem;
  margin: 0 auto;
  border-radius: 1rem;
  -webkit-box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .slider {
    border-radius: 2rem;
  }
}
.slider .left-arrow, .slider .right-arrow {
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
  width: 6rem;
  height: 6rem;
  top: 40%;
  z-index: 11;
  cursor: pointer;
}
@media (min-width: 768px) {
  .slider .left-arrow, .slider .right-arrow {
    width: 10rem;
    height: 10rem;
  }
}
.slider .left-arrow {
  left: 1rem;
}
.slider .right-arrow {
  right: 1rem;
  transform: scale(-1);
}

.slider--inner {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  transition: all 0.5s ease-in;
}
.slider--inner img {
  width: 100%;
  cursor: pointer;
  mask-image: none;
  z-index: 10;
}

.mover {
  margin-top: -17rem;
}

.alerta {
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.alerta:first-of-type {
  margin-top: 1rem;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
}
.alerta:last-of-type {
  border-bottom-left-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
}
.alerta.error {
  background-color: red;
  color: #FFFFFF;
}
.alerta.exito {
  background-color: #68a008;
  color: #FFFFFF;
}

.opciones.admin {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.opciones.admin a {
  width: 50vmin;
}

.actualizar-imagen img {
  width: 40rem;
}

.contenedor-tabla {
  overflow-x: auto;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #7ec20a;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades td {
  text-align: center;
  border-bottom: 1px solid gray;
  padding-bottom: 2.5rem;
}
table.propiedades td img {
  width: 15rem;
  display: block;
  margin: 0 auto;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url("../img/header2.jpg");
}

.webp .inicio {
  background-image: url("../img/header2.webp");
}

.header {
  padding-top: 3rem;
  background-color: #333333;
  background-size: cover;
  padding-bottom: 3rem;
}
.header.inicio {
  background-position: top left;
  padding-bottom: 0;
  mask-image: linear-gradient(black 50%, transparent 100%);
  min-height: 50rem;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
    mask-image: linear-gradient(black 30%, transparent 100%);
  }
}
.header.inicio .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header.inicio .contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}
.header .barra-contenido {
  display: flex;
  gap: 1.5rem;
}
.header .barra-contenido .logo {
  width: auto;
}
@media (min-width: 768px) {
  .header .barra-contenido .logo {
    width: 30rem;
  }
}

.derecha {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: column;
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 1rem;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}

.mobile-menu {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: auto;
  max-width: 4rem;
}

.mostrar {
  visibility: visible;
  height: auto;
  opacity: 1;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0;
  border-radius: 1.5rem;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .anuncio {
    max-width: 41rem;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  height: 100%;
  display: grid;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .anuncio .contenido-anuncio {
    max-width: 41rem;
  }
}
.anuncio .contenido-anuncio .titulo-descripcion {
  word-wrap: break-word;
}
@media (min-width: 768px) {
  .anuncio .contenido-anuncio .titulo-descripcion {
    max-width: 36.7rem;
  }
}
.anuncio .contenido-anuncio .info-propiedad {
  display: flex;
  flex-direction: column;
  justify-content: end;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  text-align: center;
  color: #68a008;
  font-weight: 700;
  font-size: 2.4rem;
}

.iconos-caracteristicas {
  margin: 2rem;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
  max-width: 40rem;
  gap: 1rem;
  margin: 0 auto;
}
.iconos-caracteristicas li {
  display: flex;
  gap: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 700;
}

.resumen-propiedad {
  align-items: center;
}

.anuncio-detalles {
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .anuncio-detalles {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  margin-top: 2.5rem;
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.8rem;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
  background-color: #e1e1e1;
  border-radius: 1rem;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog img {
  height: 100%;
}

.texto-entrada {
  padding: 1.2rem;
}
@media (min-width: 768px) {
  .texto-entrada {
    padding-left: 0;
  }
}
.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4:after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #68a008;
}
.texto-entrada span {
  color: #a8c03d;
}

.testimonial {
  background-color: #4e286d;
  font-size: 2.4rem;
  padding: 5rem;
  color: #FFFFFF;
  border-radius: 1rem;
  -webkit-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 16px -6px rgba(0, 0, 0, 0.75);
}
.testimonial blockquote {
  margin: 0;
  padding-left: 4rem;
  position: relative;
}
.testimonial blockquote:before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 10rem;
  height: 9rem;
  display: block;
  position: absolute;
  left: -5rem;
  top: -5rem;
}
.testimonial blockquote::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  bottom: -7rem;
  border-left: 2rem solid transparent;
  border-right: 2rem solid transparent;
  border-top: 2.5rem solid #4e286d;
  border-bottom: none;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.testimonial-persona {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
}
.testimonial-persona picture {
  width: auto;
}
.testimonial-persona img {
  width: 7.5rem;
  border-radius: 50%;
  border: 0.5rem solid #333333;
}

@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .nosotros h4 {
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .formulario {
